@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.site-layout {
  background-color: #000;
  width: 100%;
  height: 100%;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}
.spacer {
  top: 0px;
  height: 50px;
}



.toolbar {
    flex-direction: column;
    position: fixed;
    min-width: 5rem;
    background: #f6f6f7;
    height: 100%;
    justify-content: flex-start;
    z-index: 99999;
    display: flex;
}
.toolbar__logo{
    -webkit-tap-highlight-color: transparent;
    align-self: center;
    text-transform: uppercase;
    font-weight: 900;
    font-family: "archia";
    color: #6c757d;
    text-align: center;
    width: 2.9rem;
    font-size: .6rem;
    order: 1;
    padding-top: 2rem;
    padding-bottom: 2rem;
}
.toolbar__logo a {
    text-decoration: none;
    color: inherit;
}
.toolbar__logo span {
    color: #4c75f2;
}

.toolbar__navigation {
    order :2;
    flex-direction: column;
    position: fixed;
    min-width: 5rem;
    background: #f6f6f7;
    height: 100%;
    justify-content: flex-start;
    z-index: 99999;

}
